@import '../app.scss';

.contact{
    height: 100%;
    max-width: 1366px;
    margin: auto;
    display: flex;
    align-items: center;
    gap: 10px;
    @include mobile{
        width: 100%;
        padding: 10px;
        flex-direction: column;
      }
    .textContainer{
        flex: 1;
        display: flex;
        flex-direction: column;
        gap: 40px;

        @include mobile{
            gap: 20px;
            text-align: center;
            align-items: center;
            margin-top: 70px;
          }

        h1{
            font-size: 100px;
            line-height: 88px;
            @include mobile{
                font-size: 36px;
              }
        }
        span{
            font-weight: thin;
        }

        
    }
    .formContainer{
        flex:1;
        position: relative;
        @include mobile{
            width: 100%;
            padding: 50px;
          }
        
        .phoneSvg{
            stroke: orange;
            position: absolute;
            margin: auto;
            z-index: -1;
            @include mobile{
                display: none;
              }
        }
        form{
        display: flex;
        flex-direction: column;
        gap:20px;

        @include mobile{
           transition-duration: none !important;
          }
        

        input,textarea{
            padding: 20px;
            background-color: transparent;
            color: white;
            border: 1px solid white;
            border-radius: 8px;

            @include mobile{
                padding: 10px;
              }
        }
        button{
            border: none;
            padding: 20px;
            background-color: orange;
            cursor: pointer;
            font-weight: 500;
            border-radius: 8px;
            @include mobile{
               padding: 10px;
              }
        }
    }
    }
}