@import '../app.scss';

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');


.about{
    backdrop-filter: blur(10px);
    z-index: 3;
    border: 1px solid white;
    border-radius: 10px;
    width: 50%;
    padding: 50px;
    font-family: "Montserrat",sans-serif ;

    @include mobile{
        width: 70%;
        font-size: 15px;
        padding: 15px;
    }

    .logos{
        display: flex;
        width: 100%;

        img{
            height: 40px;
        }
    }
}
